import Navbar from "../components/Navbar";

export default function RegisterPage() {
    return (
        <>
            <Navbar />

            <section className="register-page d-flex align-items-center">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-7 col-lg-6">
                            <div className="register-card card p-4 shadow-lg">

                                {/* Project Name */}
                                <div className="text-center mb-3">
                                    <h1 className="brand-title">Cudobe</h1>
                                    <p className="brand-subtitle">
                                        Intelligent Trade Document Verification
                                    </p>
                                </div>

                                <h3 className="text-center mb-4 register-title">
                                    Create Account
                                </h3>

                                <form>

                                    <div className="mb-4">
                                        <label className="form-label d-block">Account Type</label>

                                        <div className="account-type-box">
                                            <label className="type-option">
                                                <input type="radio" name="accountType" />
                                                <span>Importer</span>
                                            </label>

                                            <label className="type-option">
                                                <input type="radio" name="accountType" />
                                                <span>Exporter</span>
                                            </label>

                                            <label className="type-option">
                                                <input type="radio" name="accountType" />
                                                <span>Both</span>
                                            </label>
                                        </div>
                                    </div>

                                    <div className="mb-3">
                                        <label className="form-label">Full Name</label>
                                        <input type="text" className="form-control" />
                                    </div>

                                    <div className="mb-3">
                                        <label className="form-label">Email</label>
                                        <input type="email" className="form-control" />
                                    </div>

                                    <div className="mb-3">
                                        <label className="form-label">Password</label>
                                        <input type="password" className="form-control" />
                                    </div>

                                    <div className="mb-3">
                                        <label className="form-label">Confirm Password</label>
                                        <input type="password" className="form-control" />
                                    </div>

                                    {/* Wide Upload Section */}
                                    <div className="mb-4">
                                        <label className="form-label">
                                            Upload Trade Documents
                                        </label>

                                        <div className="upload-box">
                                            <input
                                                type="file"
                                                name="documents"
                                                className="form-control upload-input"
                                                multiple // Allow more than one file if needed
                                                onChange={handleFileChange}
                                            />
                                            <p className="upload-hint">
                                                Upload Shipping Bill / Commercial Invoice (PDF, PNG, JPG)
                                            </p>
                                        </div>
                                    </div>

                                    <button
                                        type="submit"
                                        className="btn btn-outline-dark w-100"
                                    >
                                        <i className="fa-brands  me-2"></i>
                                        Register
                                    </button>

                                    <p className="text-center mt-3 small text-muted">
                                        Already have an account?{" "}
                                        <span className="login-link">Login</span>
                                    </p>
                                </form>

                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
